.contact-container {
  /* border: 1px solid red; */
  /* margin: auto; */
  margin: 30px;
  padding: 40px;
}
.contact-form {
  /* border: 1px solid black; */
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 40%;
  margin: auto;
}

.contact-form > button {
  display: block;
  /* width: 25%; */
  width: 100px;
  text-align: center;
  border: none;
  background-color: crimson;
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  margin: auto;
  padding: 15px 0px;
  border-radius: 5px;
  border: 1px solid #dc143c;
  cursor: pointer;
}
.contact-form > button:hover {
  transition: 0.25s ease-in;
  color: #dc143c;
  background-color: #fff;
  border: 1px solid #dc143c;
}
input,
textarea {
  padding: 20px;
  outline: none;
  border: none;
  border-radius: 5px;
  border: 1px solid gray;
}
@media screen and (max-width: 1000px) {
  .contact-form {
    width: 80%;
  }
}
@media screen and (max-width: 500px) {
  .contact-form {
    width: 100%;
  }
}
