.heading {
  padding: 100px 0 0 0;
  /* margin: 40px 0; */
  text-align: center;
  /* margin-bottom: 40px; */
  position: relative;
  animation: heading_move 2s 1;
}
@keyframes heading_move {
  from {
    top: -950px;
  }
  to {
    top: 0px;
  }
}
.heading-highlight {
  color: #dc153d;
}
